body {
  background-color: rgba(108, 95, 160, 0.149);
}

h1,
h2,
h3,
h4 {
  margin-top: 0;
  margin-bottom: 0;
}

.countries__container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

.search__title {
  font-weight: 700;
  text-align: center;
  letter-spacing: 0.03em;
  color: black;
  padding-top: 40px;
}

input {
  margin-top: 40px;
  width: 100%;
  height: 40px;
}

.country-list__item {
  cursor: pointer;
}

.render__box--flex {
  display: flex;
}

.render__item + .render__item {
  margin-left: auto;
}

.render__flag {
  width: 125px;
  height: 125px;
}

@media screen and (min-width: 420px) {
  .countries__container {
    min-width: 420px;
  }
  .render {
    padding: 50px 50px;
  }
}

@media screen and (min-width: 720px) {
  .countries__container {
    min-width: 720px;
  }
  .render {
    padding: 50px 120px;
  }
  .render__flag {
    width: 280px;
    height: 280px;
  }
}

@media screen and (max-width: 1199px) {
  .countries__container {
    font-size: 28px;
    line-height: 1.18;
  }
}

@media screen and (min-width: 1200px) {
  .countries__container {
    width: 1200px;
  }
  .render {
    padding: 50px 300px;
  }
  .search__title {
    font-size: 36px;
    line-height: 1.16;
  }
}
